<template>
  <div class="user">
    <div class="head-page" v-if="isLogin">
      <div class="head-img">
        <img :src="avatar" alt="" />
      </div>
      <div class="info">
        <div class="mobile">{{ userInfo.mobile }}</div>
        <div class="vip">
          <van-icon name="diamond-o" />
          普通会员
        </div>
      </div>
    </div>

    <div
      v-else
      class="head-page"
      @click="
        $router.push({
          path: '/login',
          query: {
            backUrl: $route.fullPath,
          },
        })
      "
    >
      <div class="head-img">
        <img src="@/assets/default-avatar.png" alt="" />
      </div>
      <div class="info">
        <div class="mobile">未登录</div>
        <div class="words">点击登录账号</div>
      </div>
    </div>

    <div class="my-asset">
      <div class="asset-left">
        <div class="asset-left-item" @click="$router.push('/wallet')">
          <span>{{ balance || 0 }}</span>
          <span>账户余额</span>
        </div>
        <div class="asset-left-item" @click="$toast('敬请期待')">
          <span>{{ points }}</span>
          <span>积分</span>
        </div>
        <div class="asset-left-item" @click="$router.push('/mycoupon')">
          <span>{{ coupon }}</span>
          <span>优惠券</span>
        </div>
      </div>
      <div class="asset-right">
        <div class="asset-right-item" @click="$router.push('/wallet')">
          <van-icon name="balance-pay" />
          <span>我的钱包</span>
        </div>
      </div>
    </div>
    <div class="order-navbar">
      <div
        class="order-navbar-item"
        @click="$router.push('/myorder?dataType=all')"
      >
        <van-icon name="balance-list-o" />
        <span>全部订单</span>
      </div>
      <div
        class="order-navbar-item"
        @click="$router.push('/myorder?dataType=payment')"
      >
        <van-icon name="clock-o" :badge="payment ? payment : null" />
        <span>待支付</span>
      </div>
      <div
        class="order-navbar-item"
        @click="$router.push('/myorder?dataType=delivery')"
      >
        <van-icon name="logistics" :badge="delivery ? delivery : null" />
        <span>待发货</span>
      </div>
      <div
        class="order-navbar-item"
        @click="$router.push('/myorder?dataType=received')"
      >
        <van-icon name="send-gift-o" :badge="received ? received : null" />
        <span>待收货</span>
      </div>
    </div>

    <div class="service">
      <div class="title">我的服务</div>
      <div class="content">
        <div class="content-item" @click="$router.push('/address')">
          <van-icon name="records" />
          <span>收货地址</span>
        </div>
        <div class="content-item" @click="$router.push('/coupon/get')">
          <van-icon name="gift-o" />
          <span>领券中心</span>
        </div>
        <div class="content-item" @click="$router.push('/mycoupon')">
          <van-icon name="gift-card-o" />
          <span>优惠券</span>
        </div>
        <div class="content-item" @click="$toast('敬请期待')">
          <van-icon name="question-o" />
          <span>我的帮助</span>
        </div>
        <div class="content-item" @click="$toast('敬请期待')">
          <van-icon name="balance-o" />
          <span>我的积分</span>
        </div>
        <div class="content-item" @click="$router.push('/refund')">
          <van-icon name="refund-o" :badge="refund ? refund : null" />
          <span>退换/售后</span>
        </div>
      </div>
    </div>

    <div class="logout-btn" v-if="isLogin">
      <button @click="userLogout">退出登录</button>
    </div>
  </div>
</template>

<script>
import { getUserInfoApi, getUserAssetsApi, getTodoCountsApi } from '@/api/userInfo.js'
import defaultAvatar from '@/assets/default-avatar.png'

export default {
  name: 'UserPage',

  created () {
    if (this.isLogin) {
      this.getUserInfo()
      this.getUserAssets()
      this.getTodoCounts()
    }
  },

  activated () {
    if (this.isLogin) {
      this.getUserInfo()
      this.getUserAssets()
      this.getTodoCounts()
    }
  },

  data () {
    return {
      isLogin: this.$store.getters.ifToken,

      userInfo: {},
      // 资产
      balance: 0,
      points: 0,
      coupon: 0,

      // 订单
      delivery: 0,
      payment: 0,
      received: 0,
      refund: 0

    }
  },

  computed: {
    avatar () {
      return this.userInfo.avatar || defaultAvatar
    }

  },

  methods: {
    async getUserInfo () {
      const { data: { userInfo } } = await getUserInfoApi()
      this.userInfo = userInfo
    },

    // 退出登录
    userLogout () {
      this.$dialog.confirm({
        message: '您确定要退出吗'
      }).then(() => {
        this.$store.dispatch('user/userLogout')
        this.balance = 0
        this.points = 0
        this.coupon = 0
        this.delivery = 0
        this.payment = 0
        this.received = 0
        this.refund = 0
        this.isLogin = null
      }).catch(() => {})
    },

    // 余额 积分 优惠券
    async getUserAssets () {
      const { data: { assets } } = await getUserAssetsApi()

      this.balance = assets.balance
      this.points = assets.points
      this.coupon = assets.coupon
    },

    // 订单状态
    async getTodoCounts () {
      const { data: { counts } } = await getTodoCountsApi()
      // console.log(counts)
      this.delivery = counts.delivery
      this.payment = counts.payment
      this.received = counts.received
      this.refund = counts.refund
    }
  }

}
</script>

<style lang="less" scoped>
.user {
  min-height: 100vh;
  background-color: #f7f7f7;
  padding-bottom: 50px;
}

.head-page {
  height: 130px;
  background: url("http://cba.itlike.com/public/mweb/static/background/user-header2.png");
  background-size: cover;
  display: flex;
  align-items: center;
  .head-img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 10px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
.info {
  .mobile {
    margin-bottom: 5px;
    color: #c59a46;
    font-size: 18px;
    font-weight: bold;
  }
  .vip {
    display: inline-block;
    background-color: #3c3c3c;
    padding: 3px 5px;
    border-radius: 5px;
    color: #e0d3b6;
    font-size: 14px;
    .van-icon {
      font-weight: bold;
      color: #ffb632;
    }
  }
}

.my-asset {
  display: flex;
  padding: 20px 0;
  font-size: 14px;
  background-color: #fff;
  .asset-left {
    display: flex;
    justify-content: space-evenly;
    flex: 3;
    .asset-left-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      span:first-child {
        margin-bottom: 5px;
        color: #ff0000;
        font-size: 16px;
      }
    }
  }
  .asset-right {
    flex: 1;
    .asset-right-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .van-icon {
        font-size: 24px;
        margin-bottom: 5px;
      }
    }
  }
}

.order-navbar {
  display: flex;
  padding: 15px 0;
  margin: 10px;
  font-size: 14px;
  background-color: #fff;
  border-radius: 5px;
  .order-navbar-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 25%;
    .van-icon {
      font-size: 24px;
      margin-bottom: 5px;
    }
  }
}

.service {
  font-size: 14px;
  background-color: #fff;
  border-radius: 5px;
  margin: 10px;
  .title {
    height: 50px;
    line-height: 50px;
    padding: 0 15px;
    font-size: 16px;
  }
  .content {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    font-size: 14px;
    background-color: #fff;
    border-radius: 5px;
    .content-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 25%;
      margin-bottom: 20px;

      .van-icon {
        font-size: 24px;
        margin-bottom: 5px;
        color: #ff3800;
      }
    }
  }
}

.logout-btn {
  button {
    width: 60%;
    margin: 10px auto;
    display: block;
    font-size: 13px;
    color: #616161;
    border-radius: 9px;
    border: 1px solid #dcdcdc;
    padding: 7px 0;
    text-align: center;
    background-color: #fafafa;
  }
}
</style>
